<template>
  <div id="app" :style="{backgroundColor: color}">
    <Header
      title="New Vue App"
      :totalNumQuestions="totalNumQuestions"
      :questionCount="index + 1"
    />
    <QuestionBox
      v-bind:questions="questions.results"
      v-bind:currentQuestion="questions.results[index]"
      v-bind:nextQuestion="nextQuestion"
    />
  </div>
</template>

<script>
import Header from './components/Header.vue'
import QuestionBox from './components/QuestionBox.vue'
import axios from 'axios'

export default {
  name: 'App',
  components: {
    Header,
    QuestionBox
  },
  data() {
    return {
      index: 0,
      questions: [],
      totalNumQuestions: 0,
      color: '#252c4a'
    }
  },
  methods: {
    nextQuestion() {
      this.index++;
    },
    getTotalNumQuestions() {
      let questions = this.questions.results;
      this.totalNumQuestions = questions.length;
    }
  },

  async mounted() {
    await axios.get('https://opentdb.com/api.php?amount=10&category=11')
      .then(function(response) {
        this.questions = response.data;
      }.bind(this))

    this.getTotalNumQuestions();
  }
}
</script>

<style>
@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@400;600;700&display=swap');

body {
  height: 100vh;
}

#app {
  font-family: 'Poppins', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #ffffff;
  display: block;
  height: 100%;
  padding-top: 40px;
}
</style>
