<template>
    <div>
        <b-container>
            <b-row>
                <b-col sm="6" offset-sm="3">
                    <h2 v-html="currentQuestion.question"></h2>
                    <b-list-group class="mt-3">
                        <b-list-group-item
                            v-for="(answer, index) in answers"
                            v-bind:key="index"
                            v-on:click="selectAnswer(index)"
                            :class="[
                                selectedIndex === index ?
                                'selected' :
                                ''
                            ]">
                            {{ answer }}
                        </b-list-group-item>
                    </b-list-group>
                    <b-row>
                        <b-col cols="6">
                            <b-button variant="primary" id="submitBtn" class="mt-3" :disabled="selectedIndex === null || answered || currentQuestion.question === questions[questions.length - 1].question" @click="submitAnswer(); nextQuestion();">Next</b-button>
                        </b-col>
                        <b-col cols="6">
                            <b-button variant="primary" id="submitBtn" class="mt-3" :disabled="currentQuestion.question !== questions[questions.length - 1].question" @click="submitAnswer(); completedQuiz();">Complete Quiz</b-button>
                        </b-col>
                    </b-row>
                    <!-- <b-button variant="primary" class="mt-3" @click="nextQuestion" :disabled="currentQuestion.question === questions[questions.length - 1].question">Next</b-button> -->
                </b-col>
            </b-row>
        </b-container>
        <b-modal ref="completed-modal" hide-footer title="You Scored">
            <div class="d-block text-center">
                <h3>You Answered {{ this.correctAnswers }} correctly</h3>
            </div>
        </b-modal>
    </div>
</template>

<script>
import _ from 'lodash'

export default {
    data() {
        return {
            selectedIndex: null,
            shuffledAnswers: [],
            correctIndex: null,
            correctAnswers: 0,
            answered: false
        }
    },
    props: {
        currentQuestion: Object,
        questions: Array,
        nextQuestion: Function,
    },
    computed: {
        answers() {
            return this.shuffledAnswers;
        }
    },
    watch: {
        currentQuestion: {
            immediate: true,
            handler() {
                this.selectedIndex = null;
                this.answered = false;
                this.shuffleAnswers();
            }
        }
    },
    methods: {
        selectAnswer(index) {
            this.selectedIndex = index;
        },
        shuffleAnswers() {
            let sAnswers = [...this.currentQuestion.incorrect_answers, this.currentQuestion.correct_answer];
            this.shuffledAnswers = _.shuffle(sAnswers);
            this.correctIndex = this.shuffledAnswers.indexOf(this.currentQuestion.correct_answer);
        },
        submitAnswer() {
            if (this.selectedIndex === this.correctIndex) {
                this.correctAnswers++;
            }
            this.answered = true;
        },
        lastQuestion() {
            if (this.currentQuestion.question === this.questions[this.questions.length - 1].question) {
                return true;
            }
            return false;
        },
        completedQuiz() {
            if (this.currentQuestion.question === this.questions[this.questions.length - 1].question) {
                this.$refs['completed-modal'].show();
            }
        }
    }
}
</script>

<style scoped>
    h2 {
        font-size: 20px;
        margin-bottom: 40px;
    }

    .list-group-item.selected {
        background: lightblue;
    }
    
    .list-group-item.correct {
        background: lightgreen;
    }

    .list-group-item {
        background: #fff;
        margin-bottom: 15px;
        border-radius: 5px;
        cursor: pointer;
        color: #34607f;
        box-shadow: 0px 3px 15px rgba(0,0,0,0.2);
    }

    .list-group-item:first-child {
        border-top-left-radius: 5px;
        border-top-right-radius: 5px;
    }

    .list-group-item:last-child {
        border-bottom-left-radius: 5px;
        border-bottom-right-radius: 5px;
    }

    /deep/ .modal-backdrop {
        opacity: 0.3;
    }

    #submitBtn {
        width: 200px;
        line-height: 2;
        border-radius: 25px;
        font-size: 18px;
        font-weight: 600;
    }
</style>